import { ErrorMessage, Field, Form, Formik } from "formik";
import * as React from "react";
import * as Yup from "yup";
import Modal from "./modal";

const Administrator = (props) => {
  const [showModal, setShowModal] = React.useState(false);

  const handleModal = () => {
    if (showModal) {
      return <Modal show={setShowModal(false)} />;
    }
  };

  const handleSubmit = (values) => {
    console.log("handleSubmit", values);
    props.next(values);
  };

  const passwordRegex = RegExp(
    "^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$"
  );
  const phoneRegex = RegExp(
    /^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/
  );

  const stepOneValidation = Yup.object({
    first_name: Yup.string().min(3).required("First Name is required"),
    last_name: Yup.string().min(3).required("last Name is required"),
    phone_number: Yup.string()
      .min(3)
      .required("last Name is required")
      .matches(phoneRegex, "Invalid phone"),
    license_number: Yup.string().min(3).required("license_number is required"),
    email_address: Yup.string()
      .email("Not a proper email")
      .required("Email is required"),
    password: Yup.string()
      .required("Password is required")
      .matches(
        passwordRegex,
        "Minimum eight characters, at least one letter, one number and one special character"
      ),
    passwordConfirmation: Yup.string().oneOf(
      [Yup.ref("password"), null],
      "Passwords must match"
    ),
  });

  const renderError = (message) => <p className='text-red-600'>{message}</p>;

  return (
    <>
      <Formik
        initialValues={props.data}
        validationSchema={stepOneValidation}
        onSubmit={handleSubmit}
      >
        {() => (
          <>
            <div className='md:col-span-1 mb-5'>
              <div className='px-4 sm:px-0'>
                <h3 className='text-lg font-medium leading-6 text-gray-900'>
                  Personal Information
                </h3>
                <p className='mt-1 text-sm text-gray-600'>
                  Use a permanent address where you can receive mail.
                </p>
              </div>
            </div>
            <div className='mt-5 md:col-span-2 md:mt-0'>
              <Form>
                <div className='overflow-hidden shadow sm:rounded-md'>
                  <div className='bg-white px-4 py-5 sm:p-6'>
                    {/* begin: Alert */}
                    {/* end: Alert */}
                    <div className='grid grid-cols-6 gap-6'>
                      {/* BEGIN: First Name */}
                      <div className='col-span-6 sm:col-span-3'>
                        <label
                          htmlFor='first-name'
                          className='block text-sm font-medium text-gray-700'
                        >
                          First name
                        </label>
                        <Field
                          name='first_name'
                          className='mt-1 block w-full border border-neutral-light-gray rounded px-4 py-2 text-sm transition-all focus:outline-none focus:ring-1 focus:ring-primary-purplish-blue
                        rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                        />
                        <ErrorMessage render={renderError} name='first_name' />
                      </div>
                      {/* END: First Name */}

                      {/* BEGIN: Last name */}
                      <div className='col-span-6 sm:col-span-3'>
                        <label
                          htmlFor='last-name'
                          className='block text-sm font-medium text-gray-700'
                        >
                          Last name
                        </label>
                        <Field
                          name='last_name'
                          className='mt-1 block w-full border border-neutral-light-gray rounded px-4 py-2 text-sm transition-all focus:outline-none focus:ring-1 focus:ring-primary-purplish-blue
                        rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                        />
                        <ErrorMessage render={renderError} name='last_name' />
                      </div>
                      {/* END: Last name */}

                      {/* begin: Email */}
                      <div className='col-span-6 sm:col-span-3'>
                        <label
                          htmlFor='first-name'
                          className='block text-sm font-medium text-gray-700'
                        >
                          Email Address
                        </label>
                        <Field
                          name='email_address'
                          className='mt-1 block w-full border border-neutral-light-gray rounded px-4 py-2 text-sm transition-all focus:outline-none focus:ring-1 focus:ring-primary-purplish-blue
                        rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                        />
                        <ErrorMessage
                          render={renderError}
                          name='email_address'
                        />
                      </div>
                      {/* end: Email */}

                      {/* BEGIN: Phone Number */}
                      <div className='col-span-6 sm:col-span-3'>
                        <label
                          htmlFor='phone-number'
                          className='block text-sm font-medium text-gray-700'
                        >
                          Phone Number
                        </label>
                        <Field
                          name='phone_number'
                          className='mt-1 block w-full border border-neutral-light-gray rounded px-4 py-2 text-sm transition-all focus:outline-none focus:ring-1 focus:ring-primary-purplish-blue
                        rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                        />
                        <ErrorMessage
                          render={renderError}
                          name='phone_number'
                        />
                      </div>
                      {/* END: Phone Number */}

                      {/* BEGIN: License Number */}
                      <div className='col-span-6 sm:col-span-3'>
                        <label
                          htmlFor='license-number'
                          className='block text-sm font-medium text-gray-700'
                        >
                          License Number
                        </label>
                        <Field
                          name='license_number'
                          className='mt-1 block w-full border border-neutral-light-gray rounded px-4 py-2 text-sm transition-all focus:outline-none focus:ring-1 focus:ring-primary-purplish-blue
                        rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                        />
                        <ErrorMessage
                          render={renderError}
                          name='license_number'
                        />
                      </div>
                      <div className='col-span-6 sm:col-span-3'>
                        <label
                          htmlFor='license-number'
                          className='mb-1 block text-sm font-medium text-gray-700'
                        >
                          Request License Number
                        </label>
                        <button
                          onClick={() => setShowModal(true)}
                          className='inline-flex justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2'
                          type='button'
                        >
                          Open Request
                          {handleModal()}
                        </button>
                        {/* <button 
                      className='inline-flex justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2'>
                        Open Request
                      </button> */}
                        {/* <Field
                        name='license_number'
                        className='mt-1 block w-full border border-neutral-light-gray rounded px-4 py-2 text-sm transition-all focus:outline-none focus:ring-1 focus:ring-primary-purplish-blue
                        rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                      />
                      <ErrorMessage
                        render={renderError}
                        name='license_number'
                      /> */}
                      </div>
                      {/* END: License Number */}

                      {/* BEGIN: Password */}
                      <div className='col-span-6 sm:col-span-3'>
                        <label
                          htmlFor='password'
                          className='block text-sm font-medium text-gray-700'
                        >
                          Password
                        </label>
                        <Field
                          name='password'
                          className='mt-1 block w-full border border-neutral-light-gray rounded px-4 py-2 text-sm transition-all focus:outline-none focus:ring-1 focus:ring-primary-purplish-blue
                        rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                        />
                        <ErrorMessage render={renderError} name='password' />
                      </div>
                      {/* END: Password */}
                      <div className='col-span-6 sm:col-span-3'>
                        <label
                          htmlFor='confirm-password'
                          className='block text-sm font-medium text-gray-700'
                        >
                          Confirm Password
                        </label>
                        <Field
                          name='passwordConfirmation'
                          className='mt-1 block w-full border border-neutral-light-gray rounded px-4 py-2 text-sm transition-all focus:outline-none focus:ring-1 focus:ring-primary-purplish-blue
                        rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm'
                        />
                        <ErrorMessage
                          render={renderError}
                          name='passwordConfirmation'
                        />
                      </div>
                    </div>
                  </div>

                  <div className='bg-gray-50 px-4 py-3 text-right sm:px-6'>
                    <button
                      type='submit'
                      className='inline-flex justify-center rounded-md border border-transparent bg-indigo-600 py-2 px-4 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2'
                    >
                      <span>Next</span>
                    </button>
                  </div>
                </div>
              </Form>
            </div>
          </>
        )}
      </Formik>
    </>
  );
};

export default Administrator;
